.featured {
    width: 100%;
    max-width: 1024px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    z-index: 1;
}

.featuredItem {
    
    position: relative;
    color: white;
    border-radius: 10px;
    overflow: hidden; /* image was overflowing in its containe
                      so to show border-radius */
    height: 250px;
}
 
.featuredImg {
    /* method to put all the image inside the container*/
    width: 100%;
    object-fit: cover;
}

.featuredTitles {
    position: absolute;
    bottom: 20px;
    left: 20px;
}