.hotelContainer {
    display : flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
}

.hotelWrapper {
    width: 100%;
    max-width: 1024px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    position: relative; /* parent's made*/
}

.bookNow {
    /*whenever using absolute make parent's position relative*/
    position: absolute;
    top: 10px;
    right: 0px;
    border: none;
    padding: 10px 20px;
    border: none;
    background-color: #0071c2;
    color: white;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
}

.hotelTitle {
    font-size: 24px;
}

.hotelAddress {
    font-size: 12px;
    display: flex;
    align-items: center;
    gap: 10px;
}

.hotelDistance {
    color : #0071c2;
    font-weight: 500;
}

.hotelPriceHighlight {
    color : #008009;
    font-weight: 500;
}

.hotelImages {
    display: flex;
    flex-wrap: wrap; /*this is done to prevent img from getting in a single line and in case of overflow it will adjust */
    justify-content: space-between;
}

.hotelImgWrapper {
    width: 33%;
}

.hotelImg {
    width: 100%;
    object-fit: cover;
}

.hotelDetails {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-top: 20px;
}

.hotelDetailsTexts {
    flex:3;
}

.hotelDesc {
    font-size: 14px;
    margin-top: 20px;
}

.hotelDetailsPrice {
    flex: 1;
    background-color: #ebf3ff;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.hotelDetailsPrice>h1 {
   font-size : 18px;
   color : #555;
}

.hotelDetailsPrice>span {
   font-size: 14px;
}

.hotelDetailsPrice>h2 {
    font-weight: 300;
}

.hotelDetailsPrice>button {
    border: none;
    padding: 10px 20px;
    border: none;
    background-color: #0071c2;
    color: white;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
}

.slider {
    position: sticky;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.426);
    z-index: 999;
    display: flex;
    align-items: center;
}

.sliderWrapper {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sliderImg {
    width: 80%;
    height: 80vh;
}

.close {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 30px;
    color: lightgray;
    cursor: pointer;
}

.arrow {
    margin : 20px;
    font-size: 50px;
    color: lightgray;
    cursor: pointer;
}